/* Форма - цель*/

/* Похудение, набор массы, не знаю*/
.cat-goal {
  display: flex;
  text-align: left;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: $form-text-font-size;
  line-height: $form-text-line-height;
  text-transform: uppercase;
  color: $form-goal-color;
  padding: 0;
  margin-left: 20px;
  margin-bottom: 35px;
  margin-top: 56px;
  height: 116px;

  @media (min-width: $tablet-width) {
    margin-top: 46px;
    margin-bottom: 68px;
    min-width: 628px;
    margin-left: 20px;
    font-size: $form-text-font-size-tablet-desktop;
    line-height: $form-text-line-height-tablet-desktop;
    width: 628px;
    height: 200px;
    border: 2px solid #f2f2f2;
    float: left;
  }

  @media (min-width: $desktop-width) {
    margin-left: 45px;
    margin-top: -265px;
    height: 200px;
    font-size: $form-text-font-size-tablet-desktop;
    line-height: $form-text-line-height-tablet-desktop;
    width: 568px;
    float: right;
  }
}

/*Список общий ul */
.cat-goal__list {
  @media (min-width: $tablet-width) {
    padding-top: 27px;
    padding-left: 30px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 27px;
    padding-left: 30px;
    width: 568px;
  }
}

/* Список li*/
.cat-goal__item {
  margin-bottom: 13px;
  height: 31px;
  position: relative;
  display: block;

  @media (min-width: $tablet-width) {
    margin-bottom: 23px;
    margin-left: 12px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 23px;
  }
}

.cat-goal__radio {
  position: relative;
  display: block;
}
